function HideOnScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger();
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}



function CustomTabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          {children}
        </Box>
      )}
    </div>
  );
}


export default function TopTabs() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [tabCategory, setTabCategory] = React.useState('character');
  const [anchorEl, setAnchorEl] = useState(null);

  const handleChange = (event, newValue) => {
    setTabCategory(newValue);
  };

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const tabLabels = [
    { label: "キャラ", value: "character" },
    { label: "スキル", value: "skill" },
    { label: "メモリア", value: "memoria" },
    { label: "アイテム", value: "item" },
    { label: "素材", value: "material" },
    { label: "特性", value: "gift" },
    { label: "調合", value: "alchemy" },
    { label: "クエスト", value: "quest" },
  ];

  return (
    <Box sx={{ width: '100%' }}>

      <HideOnScroll>
        <AppBar position="fixed">
          <Toolbar>
            <div style={{ marginRight: '10px' }}>
              <Typography variant="h6" noWrap sx={{ fontSize: '16px',fontWeight:'bold' }}> {/* テキストサイズを小さく設定 */}
                レスレリ便利ツール
              </Typography>
            </div>
            {isMobile ? (
              <>
                <Button
                  edge="end"
                  color="inherit"
                  aria-label="menu"
                  onClick={handleMenuOpen}
                >
                  <MenuIcon />
                  <span style={{ marginLeft: '4px', fontSize: '16px' }}>
                    {tabLabels.find(tab => tab.value === tabCategory)?.label}
                  </span>
                </Button>
                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl)}
                  onClose={handleMenuClose}
                >
                  {tabLabels.map((tab, index) => (
                    <MenuItem key={index} onClick={() => { setTabCategory(tab.value); handleMenuClose(); }}>
                      {tab.label}
                    </MenuItem>
                  ))}
                </Menu>
              </>
            ) : (
              <Tabs value={tabCategory} onChange={handleChange}>
                {tabLabels.map((tab, index) => (
                  <Tab key={index} label={tab.label} value={tab.value} sx={{}}/>
                ))}
              </Tabs>
            )}
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />

      <div style={{ paddingLeft: '4px', fontSize: '14px' }}>
        ・2023/12/23_新キャラ、メモリア、アイテム、素材追加。ライザからの挑戦状3Lv2まで反映。<br />
        ・素材の所持/未所持を設定できるようにし、調合で「所持素材のみ」を選択可能に。<br />
      </div>

      {tabLabels.map((tab, index) => (
        <CustomTabPanel key={index} value={tabCategory} index={tab.value}>
          <TabContents category={tab.value} onChangeTab={setTabCategory} />
        </CustomTabPanel>
      ))}

      <ButtonToClearLocalStorage />
    </Box>
  );
}